#container {
  background: #fff;
  width: 768px;
  max-width: 100%;
  min-height: 540px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  text-align: center;
}
#container .form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: 0.6s ease-in-out;
}
#container .form-container form {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0px 50px;
  height: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
}
#container .form-container form .social-container {
  margin: 20px 0;
}
#container .form-container form .social-container a {
  border: 1px solid #ddd;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  height: 40px;
  width: 40px;
  text-decoration: none;
}
#container .form-container form input {
  margin: 10px;
}

button {
  text-transform: uppercase;
  transition: transform 80ms ease-in;
}

button:active {
  transform: scale(0.95);
}

button:focus {
  outline: none;
}

.ghost {
  background: transparent;
  border: 1.4px solid #fff;
}
.ghost:focus {
  outline: none;
}

.sign-in-container {
  left: 0;
  width: 50%;
  z-index: 1;
}

.sign-up-container {
  left: 0;
  width: 50%;
  z-index: 1;
  opacity: 0;
}

.overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 100;
}

.overlay {
  background: #55628e;
  color: #fff;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}
.overlay .overlay-panel {
  position: absolute;
  top: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0px 40px;
  width: 50%;
  text-align: center;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}
.overlay .overlay-right {
  right: 0;
  transform: translateX(0);
}
.overlay .overlay-left {
  transform: translateX(-20%);
}

#container.right-panel-active .sign-in-container {
  transform: translateX(100%);
}

#container.right-panel-active .overlay-container {
  transform: translateX(-100%);
}

#container.right-panel-active .sign-up-container {
  transform: translateX(100%);
  opacity: 1;
  z-index: 15;
}

#container.right-panel-active .sign-in-container {
  opacity: 0;
}

#container.right-panel-active .overlay {
  transform: translateX(50%);
}

#container.right-panel-active .overlay-left {
  transform: translateX(0);
}

#container.right-panel-active .overlay-right {
  transform: translateX(-10%);
}

#pass-checkbox-container p {
  font-size: 12px;
  margin-top: 6px;
  color: #333;
}

.social a {
  color: black;
}
